			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="150px"
		>
			<template >
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="景区名称" prop="jingqumingcheng">
					<el-input v-model="ruleForm.jingqumingcheng" placeholder="景区名称" clearable  :readonly="ro.jingqumingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="景区名称" prop="jingqumingcheng">
					<el-input v-model="ruleForm.jingqumingcheng" placeholder="景区名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="景区地址" prop="jingqudizhi">
					<el-input v-model="ruleForm.jingqudizhi" placeholder="景区地址" clearable  :readonly="ro.jingqudizhi"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="景区地址" prop="jingqudizhi">
					<el-input v-model="ruleForm.jingqudizhi" placeholder="景区地址" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="select" v-if="type!='info'"  label="最佳季节" prop="zuijiajijie">
					<el-select :disabled="ro.zuijiajijie" v-model="ruleForm.zuijiajijie" placeholder="请选择最佳季节" >
						<el-option
							v-for="(item,index) in zuijiajijieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="最佳季节" prop="zuijiajijie">
					<el-input v-model="ruleForm.zuijiajijie"
						placeholder="最佳季节" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="住宿推荐" prop="zhusutuijian">
					<el-input v-model="ruleForm.zhusutuijian" placeholder="住宿推荐" clearable  :readonly="ro.zhusutuijian"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="住宿推荐" prop="zhusutuijian">
					<el-input v-model="ruleForm.zhusutuijian" placeholder="住宿推荐" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="美食推荐" prop="meishituijian">
					<el-input v-model="ruleForm.meishituijian" placeholder="美食推荐" clearable  :readonly="ro.meishituijian"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="美食推荐" prop="meishituijian">
					<el-input v-model="ruleForm.meishituijian" placeholder="美食推荐" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="预算天数" prop="yusuantianshu">
					<el-input v-model="ruleForm.yusuantianshu" placeholder="预算天数" clearable  :readonly="ro.yusuantianshu"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="预算天数" prop="yusuantianshu">
					<el-input v-model="ruleForm.yusuantianshu" placeholder="预算天数" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-if="type!='info'"  label="预算花费" prop="yusuanhuafei">
					<el-input v-model="ruleForm.yusuanhuafei" placeholder="预算花费" clearable  :readonly="ro.yusuanhuafei"></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else class="input" label="预算花费" prop="yusuanhuafei">
					<el-input v-model="ruleForm.yusuanhuafei" placeholder="预算花费" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="upload" v-if="type!='info' && !ro.jingqutupian" label="景区图片" prop="jingqutupian">
					<file-upload
						tip="点击上传景区图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.jingqutupian?ruleForm.jingqutupian:''"
						@change="jingqutupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="upload" v-else-if="ruleForm.jingqutupian" label="景区图片" prop="jingqutupian">
					<img v-if="ruleForm.jingqutupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jingqutupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jingqutupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="date" v-if="type!='info'" label="发布时间" prop="fabushijian">
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.fabushijian" 
						type="datetime"
						:readonly="ro.fabushijian"
						placeholder="发布时间"
					></el-date-picker>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' class="input" v-else-if="ruleForm.fabushijian" label="发布时间" prop="fabushijian">
					<el-input v-model="ruleForm.fabushijian" placeholder="发布时间" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-if="type!='info'"  label="游玩路线" prop="youwanluxian">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.youwanluxian" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else-if="ruleForm.youwanluxian" label="游玩路线" prop="youwanluxian">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","minWidth":"400px","color":"inherit","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.youwanluxian"></span>
                </el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-if="type!='info'"  label="周边介绍" prop="zhoubianjieshao">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.zhoubianjieshao" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"border":"0px solid #eee","padding":"0px 0","margin":"0 0 20px 0","color":"inherit","borderRadius":"0px","background":"none","width":"100%","fontSize":"inherit"}' v-else-if="ruleForm.zhoubianjieshao" label="周边介绍" prop="zhoubianjieshao">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","minWidth":"400px","color":"inherit","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.zhoubianjieshao"></span>
                </el-form-item>
			<el-form-item :style='{"padding":"10px 0 20px","boxShadow":"0 1px 1px rgba(0,0,0,0.1)","margin":"30px 0","alignItems":"center","textAlign":"center","background":"linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(240,243,244,.5) 100%)","display":"flex","width":"100%","perspective":"320px","-webkitPerspective":"320px","fontSize":"48px","justifyContent":"flex-start"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-tijiao16" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					保存
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-quxiao09" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-fanhui01" :style='{"margin":"0 2px","fontSize":"18px","color":"inherit","display":"none"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
export default {
	data() {
		return {
			id: '',
			type: '',
			
			
			ro:{
				jingqumingcheng : false,
				jingqudizhi : false,
				zuijiajijie : false,
				zhusutuijian : false,
				meishituijian : false,
				yusuantianshu : false,
				yusuanhuafei : false,
				youwanluxian : false,
				zhoubianjieshao : false,
				jingqutupian : false,
				fabushijian : false,
			},
			
			
			ruleForm: {
				jingqumingcheng: '',
				jingqudizhi: '',
				zuijiajijie: '',
				zhusutuijian: '',
				meishituijian: '',
				yusuantianshu: '',
				yusuanhuafei: '',
				youwanluxian: '',
				zhoubianjieshao: '',
				jingqutupian: '',
				fabushijian: '',
			},
		
			zuijiajijieOptions: [],

			
			rules: {
				jingqumingcheng: [
				],
				jingqudizhi: [
				],
				zuijiajijie: [
				],
				zhusutuijian: [
				],
				meishituijian: [
				],
				yusuantianshu: [
				],
				yusuanhuafei: [
				],
				youwanluxian: [
				],
				zhoubianjieshao: [
				],
				jingqutupian: [
				],
				fabushijian: [
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
		this.ruleForm.fabushijian = this.getCurDateTime()
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='jingqumingcheng'){
							this.ruleForm.jingqumingcheng = obj[o];
							this.ro.jingqumingcheng = true;
							continue;
						}
						if(o=='jingqudizhi'){
							this.ruleForm.jingqudizhi = obj[o];
							this.ro.jingqudizhi = true;
							continue;
						}
						if(o=='zuijiajijie'){
							this.ruleForm.zuijiajijie = obj[o];
							this.ro.zuijiajijie = true;
							continue;
						}
						if(o=='zhusutuijian'){
							this.ruleForm.zhusutuijian = obj[o];
							this.ro.zhusutuijian = true;
							continue;
						}
						if(o=='meishituijian'){
							this.ruleForm.meishituijian = obj[o];
							this.ro.meishituijian = true;
							continue;
						}
						if(o=='yusuantianshu'){
							this.ruleForm.yusuantianshu = obj[o];
							this.ro.yusuantianshu = true;
							continue;
						}
						if(o=='yusuanhuafei'){
							this.ruleForm.yusuanhuafei = obj[o];
							this.ro.yusuanhuafei = true;
							continue;
						}
						if(o=='youwanluxian'){
							this.ruleForm.youwanluxian = obj[o];
							this.ro.youwanluxian = true;
							continue;
						}
						if(o=='zhoubianjieshao'){
							this.ruleForm.zhoubianjieshao = obj[o];
							this.ro.zhoubianjieshao = true;
							continue;
						}
						if(o=='jingqutupian'){
							this.ruleForm.jingqutupian = obj[o];
							this.ro.jingqutupian = true;
							continue;
						}
						if(o=='fabushijian'){
							this.ruleForm.fabushijian = obj[o];
							this.ro.fabushijian = true;
							continue;
						}
				}
			}
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
            this.zuijiajijieOptions = "春季,夏季,秋季,冬季".split(',')
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `lvyougonglve/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.youwanluxian = this.ruleForm.youwanluxian.replace(reg,'../../../springbootsiiny4vh/upload');
        this.ruleForm.zhoubianjieshao = this.ruleForm.zhoubianjieshao.replace(reg,'../../../springbootsiiny4vh/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
	if(this.ruleForm.jingqutupian!=null) {
		this.ruleForm.jingqutupian = this.ruleForm.jingqutupian.replace(new RegExp(this.$base.url,"g"),"");
	}
var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "lvyougonglve/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `lvyougonglve/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.lvyougonglveCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `lvyougonglve/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.lvyougonglveCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.lvyougonglveCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    jingqutupianUploadChange(fileUrls) {
	    this.ruleForm.jingqutupian = fileUrls;
    },
  }
};
</script>
<style lang="scss" scoped>
	.amap-wrapper {
		width: 100%;
		height: 500px;
	}
	
	.search-box {
		position: absolute;
	}
	
	.el-date-editor.el-input {
		width: auto;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
	  	  padding: 0 10px 0 0;
	  	  color: #333;
	  	  font-weight: 500;
	  	  display: inline-block;
	  	  width: 150px;
	  	  font-size: inherit;
	  	  line-height: 40px;
	  	  text-align: right;
	  	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__content {
	  margin-left: 150px;
	}
	
	.add-update-preview .el-input /deep/ .el-input__inner {
	  	  padding: 0 12px;
	  	  color: inherit;
	  	  font-size: 14px;
	  	  border-color: #e3e3e3;
	  	  border-radius: 3px;
	  	  box-shadow: 0 0 0px rgba(64, 158, 255, .5);
	  	  outline: none;
	  	  background: none;
	  	  width: auto;
	  	  border-width: 1px;
	  	  border-style: solid;
	  	  min-width: 350px;
	  	  height: 39px;
	  	}
	.add-update-preview .el-input-number /deep/ .el-input__inner {
		text-align: left;
	  	  padding: 0 12px;
	  	  color: inherit;
	  	  font-size: 14px;
